/**
 * 
 */
export const ERR_TYPE={
    DEFAULT:{
        code: 1,
        errMsg: ''
    },
    SUCCESS:{
        code: 100,
        errMsg: 'success'
    },
    API_RESP_ERR:{
        code: 300,
        errMsg: 'api '
    },
    VALID_AGENT:{
        code: 1000,
        errMsg: ''
    },
    WX_INVALID:{
        code: 1001,
        errMsg: 'WeixinJSBridge '
    },
    PAY_ERR:{
        code: 1002,
        errMsg: ''
    },
    PAY_CANCEL:{
        code: 1003,
        errMsg: ''
    }
}

export class ChargerError extends Error{
    /**
     * 
     */
    constructor(errType = ERR_TYPE.DEFAULT, message=errType.errMsg,...params){
        super(message,...params);
        if(Error.captureStackTrace){
            Error.captureStackTrace(this, ChargerError);
        }
        this.errCode = errType.code
        this.date = new Date();
    }
}